/* Cell styles */
.cell {
    position: relative;
    width: 40px;
    height: 40px;
    border: 1px solid #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    transition: background-color 0.3s, border-color 0.3s;
}

.cell input {
    width: 100%;
    height: 100%;
    text-align: center;
    border: none;
    font-size: 16px;
    font-weight: bold;
    background-color: transparent;
    outline: none;
    transition: color 0.3s;
}

.cell.frozen input {
    background-color: #e0e0e0;
    color: #555;
    pointer-events: none !important;
}

.cell.completed input {
    background-color: #555;
    color: #00eeaa !important;
    pointer-events: none !important;
}

.cell-hint {
    position: absolute;
    top: -15px;
    left: -5px;
    font-size: 12px;
    color: #555;
}

.cell-number {
    position: absolute;
    top: 2px;
    left: 2px;
    font-size: 10px;
    color: #000;
}

/* Responsive styles */
@media (max-width: 768px) {
    .cell {
        width: 30px;
        height: 30px;
        font-size: 14px;
    }
}